<template>
    <div class="el-aside">
        <div>
            <img src="../../../assets/005.png" style="width: 222px" alt="jz_plateform">
        </div>
        <el-menu
                class="el-menu-vertical-demo"
                background-color="#303133"
                text-color="#FFFFFF"
                active-text-color="#ffd04b"
                :default-active="$route.path"
                router>
            <el-menu-item  index="/home">
                <i class="el-icon-menu"></i>
                <span slot="title">首 页</span>
            </el-menu-item>
            <template v-for="item in JSON.parse(munes).userMenus">
                <el-menu-item v-if="item.chilren == 'false'" :key="item.path" :index="item.path">
                    <i :class="item.icon"></i>
                    <span slot="title">{{item.title}}</span>
                </el-menu-item>
            </template>
        </el-menu>
    </div>
</template>

<script>
export default {
    data() {
        return {}
    },
    computed:{
        munes(){
            if(localStorage?.getItem('luYou')){
                return localStorage?.getItem('luYou')
            }else {
                this.$router.push("/login")
                return JSON.stringify({userMenus:{}})
            }
        }
    }
}
</script>

<style lang="scss" scoped>
.el-menu{
    height:100vh;
    border-right: 0;
    background-color: #4e767287;
}
.el-aside{
    background-color: #4e767287;
}

</style>
