<template>
    <div class="app-header">
      <h2>兼职✪PLATFORM</h2>
        <el-dropdown>
            <span class="el-dropdown-link">
              <span class="text">账号信息</span>
                <el-avatar
                    shape="circle"
                    size="medium"
                    src="https://cube.elemecdn.com/3/7c/3ea6beec64369c2642b92c6726f1epng.png">
                </el-avatar>
                <i class="el-icon-arrow-down el-icon--right"></i>
            </span>
            <el-dropdown-menu slot="dropdown">
                <el-dropdown-item>{{useriinnfo}}</el-dropdown-item>
                <el-dropdown-item divided @click.native="handleLogout">退出登陆</el-dropdown-item>
            </el-dropdown-menu>
        </el-dropdown>
    </div>
</template>

<script>
export default {
    name:'AppHeader',
    created() {
        this.loadUser()
    },
    data(){
        return {
            useriinnfo:null
        }
    },
    methods:{
        // 获取用户名
        async loadUser(){
            this.useriinnfo = localStorage.username
        },
        // 退出登陆
        handleLogout(){
            this.$confirm('退出登陆, 是否继续?', '提示', {
                confirmButtonText: '确定',
                cancelButtonText: '取消',
                type: 'warning'
            }).then(() => {
                // 清除本地存储的信息
                localStorage.clear()
                // 跳转至登陆页
                this.$router.push('/login')
                this.$message.success('已退出')
            }).catch(() => {
                this.$message({
                    type: 'info',
                    message: '取消退出'
                })
            })
        }
    }
}
</script>

<style lang="scss" scoped>
.app-header{
    height: 80%;
    display:flex;
    align-items: center;
    justify-content: space-between;
.el-dropdown-link{
        display: flex;
        align-items: center;
    }
}
.text{
  color: black;
  font-size: 16px;
  margin-right: 10px;
}
</style>
